import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ModePassation } from 'src/app/enums/mode.passation.enum';
import { TypeProcedure } from 'src/app/enums/type.procedure.enum';
import { FormeMarche } from 'src/app/enums/forme.marche.enum';
import { Caractere } from 'src/app/enums/caractere.enum';
import { FormControl } from '@angular/forms';

export interface DialogData {
  animal: string;
  name: string;
}


@Component({
  selector: 'app-create-elaboration-cdc',
  templateUrl: './create-elaboration-cdc.component.html',
  styleUrls: ['./create-elaboration-cdc.component.css']
})
export class CreateElaborationCDCComponent implements OnInit {
  disabled: boolean = false;
  intituleOperation: string; // "Intitul de l'opration"
  checkedIntituleProjetDifferent: boolean = false;

  //current date
  date = new FormControl(new Date());

  //ModesPassation
  modesPassation = Object.keys(ModePassation).slice(0)
  parseValueModePassation(value : string) {
    return ModePassation[value].toString();
  }

  //TypesProcedure
  typesProcedure = Object.keys(TypeProcedure).slice(0)
  parseValueTypeProcedure(value : string) {
    return TypeProcedure[value].toString();
  }

  //FormesMarches
  formesMarche = Object.keys(FormeMarche).slice(0)
  parseValueFormeMarche(value : string) {
    return FormeMarche[value].toString();
  }

  //caracteresMarches
  caracteresMarche = Object.keys(Caractere).slice(0)
  parseValueCaractereMarche(value : string) {
    return Caractere[value].toString();
  }

  

  constructor(
    public dialogRef: MatDialogRef<CreateElaborationCDCComponent>,
    @Inject(MAT_DIALOG_DATA) public serviceContractant: string) {
    console.log(serviceContractant)
  }

  createAdditif(): void {
    this.dialogRef.close({
      intituleOperation: "this.name"
    });
  }

  onNoClick(): void {
    this.dialogRef.close({
      intituleOperation: "this.name"
    });
  }

  ngOnInit() {
  }

}
