import { Component, ViewChild, ViewChildren, QueryList, Input } from '@angular/core';
import { MatTable, MatDialog } from '@angular/material';
import { PassageCommissionCompetente } from 'src/app/models/passage.commission.competente.model';
import { Additif } from 'src/app/models/additif.model';
import { CreateAdditifComponent } from 'src/app/dialogs/create-additif/create-additif.component';
import { CreatePassageCommissionCompetenteComponent } from 'src/app/dialogs/create-passage-commission-competente/create-commission-passage-competente.component';
import { ElaborationCDC } from 'src/app/models/elaboration.cdc.model';
import { Caractere } from 'src/app/enums/caractere.enum';
import { TypeProcedure } from 'src/app/enums/type.procedure.enum';


@Component({
  selector: 'app-elaboration-cdc',
  templateUrl: './elaboration_cdc.component.html',
  styleUrls: ['./elaboration_cdc.component.css']
})

export class ElaborationCDCComponent {

  @ViewChild("tablePassagesCommissionCompetenteCDC", {static: true}) tablePassagesCommissionCompetenteCDC: MatTable<PassageCommissionCompetente>;
  @ViewChildren("tablePassagesCommissionCompetenteAdditif") tablesPassagesCommissionCompetenteAdditif: QueryList<MatTable<PassageCommissionCompetente>>;

  @Input()
  elaborationCDC : ElaborationCDC

  isPassageCommissionMandatory():boolean{
    return this.elaborationCDC.typeProcedure == TypeProcedure.PF;
  }

  passageCommissionChecked:boolean;
  additifsChecked:boolean = false;

  passagesCommissionCompetenteAdditifs: Map<string, Array<PassageCommissionCompetente>> = new Map() ;
  passagesCommissionCompetenteCDC: Array<PassageCommissionCompetente> = []
  

  displayedColumnsPassagesCommissionCompetente: string[] = [
    'dateDepotSecretariatCommission',
    'datePassageCommission',
    'decision',
    'dateLeveeReserves',
    'PVPath',
    'dateDecision',
    'numero',
    'decisionPath'
  ]

  constructor(public dialog: MatDialog) { }

  isProjetCDCUploaded():boolean{
    return this.elaborationCDC.pathProjetCDC != null
  }

  isPassageCommissionInProgress(): boolean {
    return this.elaborationCDC.passagesCommissionProjetCDC && this.elaborationCDC.passagesCommissionProjetCDC.length>0
  }

  isPassageCommissionDisabled():boolean {
    // disabled if
    // - procedure formalisee
    // - ou il y a deja des passage commission en cours.
    return this.isPassageCommissionMandatory() || this.isPassageCommissionInProgress()

  }

  ngOnInit() {
    this.passageCommissionChecked = this.isPassageCommissionDisabled();
    this.additifsChecked = false
  }

  additifs: Array<Additif> = []

  openDialogCreateAdditif(): void {
    const dialogRef = this.dialog.open(CreateAdditifComponent, {
      width: '800px',
      data: "nouveauAdditif"
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      console.log(result);
      //this.operations.push(result);
    });

    this.additifs.push(Additif.getDummy())
  }

  openDialogCreatePassageCommissionCompetenteAdditifs(numero:string): void {
    const dialogRef = this.dialog.open(CreatePassageCommissionCompetenteComponent, {
      width: '800px',
      data: "nouveauAdditif"
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      console.log(result);
      //this.operations.push(result);
    });

    //this.passagesCommissionCompetenteAdditifs.get(numero).push(PassageCommissionCompetente.getDummy())

    if (this.passagesCommissionCompetenteAdditifs.has(numero)){
      const passages = this.passagesCommissionCompetenteAdditifs.get(numero);
      passages.push(PassageCommissionCompetente.getDummy());
    } else {
      const passages: Array<PassageCommissionCompetente> = []
      passages.push(PassageCommissionCompetente.getDummy())
      this.passagesCommissionCompetenteAdditifs.set(numero,passages)
    }

    //console.log(this.passagesCommissionCompetenteAdditifs)
    if(this.tablesPassagesCommissionCompetenteAdditif){
      this.tablesPassagesCommissionCompetenteAdditif.forEach( (element) => {
        //don't render if empty table
        if (element.dataSource){
          element.renderRows();
        }
      });
    }
  }

  openDialogCreatePassageCommissionCompetenteCDC(): void {
    const dialogRef = this.dialog.open(CreatePassageCommissionCompetenteComponent, {
      width: '800px',
      data: "nouveauPassageCommComp"
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      console.log(result);
      //this.operations.push(result);
    });

    this.passagesCommissionCompetenteCDC.push(PassageCommissionCompetente.getDummy())
    //console.log(this.litiges);
    this.tablePassagesCommissionCompetenteCDC.renderRows();

  }

  isInternational(){
    return this.elaborationCDC.caractere == Caractere.INTERNATIONAL
  }

}
