import { ModePassation } from '../enums/mode.passation.enum'
import { TypeProcedure } from '../enums/type.procedure.enum'
import { FormeMarche } from '../enums/forme.marche.enum'
import { Caractere } from '../enums/caractere.enum'
import { PassageCommissionCompetente } from './passage.commission.competente.model'

export class ElaborationCDC {
    modePassation : ModePassation
    typeProcedure : TypeProcedure
    formeMarche: FormeMarche
    caractere: Caractere //national ou international
    dateCreation: Date

    pathProjetCDC:string
    passagesCommissionProjetCDC: PassageCommissionCompetente[]

    constructor (
        modePassation : ModePassation,
        typeProcedure : TypeProcedure,
        formeMarche: FormeMarche,
        caractere: Caractere,
        dateCreation: Date
    ) {
        this.modePassation = modePassation,
        this.typeProcedure = typeProcedure,
        this.formeMarche = formeMarche,
        this.caractere = caractere  
        this.dateCreation = dateCreation
        this.passagesCommissionProjetCDC = []
        this.passagesCommissionProjetCDC.push(PassageCommissionCompetente.getDummy())
    }

    static getDummy(): ElaborationCDC {
        return new ElaborationCDC(
            ModePassation.AO,
            TypeProcedure.PAC,
            FormeMarche.MA,
            Caractere.INTERNATIONAL,
            new Date()
        )
    }
}